import React, { useState, useEffect } from 'react';
import './resume.scss';
import { fetchContentfulEntries, fetchContentfulAsset } from '../../contentfulAPI';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';
import OgDate from '../../components/date';

const Resume = (props) => {

  const [resumeData, setResumeData] = useState([]);
  const [educationData, setEducationData] = useState([]);
  const [resumePdfData, setResumePdfData] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {

        const positionsResponse = await fetchContentfulEntries('positions', 'fields.startDate');
        setResumeData(positionsResponse.items);
  
        const educationResponse = await fetchContentfulEntries('education', 'fields.startDate');
        setEducationData(educationResponse.items);
  
        const assetResponse = await fetchContentfulAsset('NC86MeyVtmEq2sQkgQTlc');
        setResumePdfData(assetResponse);

        setIsLoaded(true);

      } catch (error) {
        console.error(error);
      } finally {
        setIsLoading(false);
      }
    };
  
    fetchData();
  }, []);
  

  useEffect(() => {
    if (isLoaded) {
      console.log("resumePdfData ", resumePdfData)
    }
  }, [isLoaded]);

  const formatDate = (dateString) => {
    if(!dateString){ return 'PRESENT' }
    const date = new Date(dateString);
    const year = date.getFullYear();
    const monthIndex = date.getMonth();
    const monthNames = [
      "January", "February", "March", "April", "May", "June", 
      "July", "August", "September", "October", "November", "December"
    ];
    const monthName = monthNames[monthIndex];
    return `${monthName} ${year}`;
  };
  
  return (
    <article className="resume container container-sm">

        <section className="container art__stagger-in art__stagger-out download-link d-flex align-items-center justify-content-center">
          <a href={resumePdfData?.fields?.file?.url} target="_blank">
            Download the PDF  
          </a>
        </section>

        <div className=''>

          <section className="art__stagger-in art__stagger-out">
            <h1 className='w-border'>Professional Experience</h1>
          </section>

          {resumeData.map((item,i) =>
            <section key={i} className={i >= (resumeData.length - 4) ? "art__stagger-in art__stagger-out resume-item" : "resume-item"}>
              
              <div className="resume-item__headline-block d-flex align-items-end justify-content-between">
                <h2 className="headline">{item?.fields?.title}</h2>
                <p>
                  <i><OgDate date={item?.fields?.startDate}/></i>&nbsp;-&nbsp;<i><OgDate date={item?.fields?.endDate}/></i>
                </p>
              </div>

              <div className="d-flex align-items-center justify-content- resume-item__employer-block">
                <h3 className="text-uppercase">{item?.fields?.company}</h3>&nbsp;
                {item?.fields?.secondaryCompany ? <p className=""><i>({item?.fields?.secondaryCompany})</i></p> : null}
              </div>

              <div className='resume-item__desc-block'>
                <div className="resume-item__description p-indent">
                  {documentToReactComponents(item?.fields?.description)}
                </div>

                <div className="resume-item__badges">
                  <div className="wrapper">
                    {item?.fields?.tags.map((tag, j) =>
                      <span key={j} className={tag.type ? (tag.type + " badge") : "badge"}>{tag.title}</span>
                    )}
                  </div>
                </div>
              </div>

            </section>
          ).reverse()}

          <section className="">
            <h1 className='w-border'>Education</h1>
          </section>

          {educationData.map((item,i) =>
            <section key={i} className="resume-item">
              
              <div className="resume-item__headline-block d-flex align-items-end justify-content-between">
                <h2 className="headline">{item?.fields?.educationLevel}</h2>
                <p>
                  <i>{formatDate(item?.fields?.startDate)}</i>&nbsp;-&nbsp;<i>{formatDate(item?.fields?.endDate)}</i>
                </p>
              </div>

              <div className="d-flex align-items-center justify-content- resume-item__employer-block">
                <h3 className="text-uppercase">{item?.fields?.school}</h3>
              </div>

              <p className="resume-item__description p-indent"><small>GPA:</small>&nbsp;<b>{item?.fields?.gpa}</b></p>
              <div className="resume-item__description p-indent">
                {documentToReactComponents(item?.fields?.description)}
              </div>

              <div className="resume-item__badges">
                <div className="wrapper">
                  {item?.fields?.tags.map((tag, j) =>
                    <span key={j} className={tag.type ? (tag.type + " badge") : "badge"}>{tag.title}</span>
                  )}
                </div>
              </div>


            </section>
          )}

        </div>

    </article>
  );

};

export default Resume;
